import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowLeft, Play, Pause } from 'lucide-react';
import { CONSTANTS } from './data';

interface KombuchaProcessPageProps {
  onBack: () => void;
  lang: 'en' | 'zh';
}

const KombuchaProcessPage: React.FC<KombuchaProcessPageProps> = ({ onBack, lang }) => {
  const [currentStep, setCurrentStep] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);

  const t = CONSTANTS.I18N;
  const processSteps = CONSTANTS.KOMBUCHA_PROCESS_STEPS;

  useEffect(() => {
    let interval: NodeJS.Timeout;
    if (isPlaying) {
      interval = setInterval(() => {
        setCurrentStep((prev) => (prev + 1) % processSteps.length);
      }, 3000);
    }
    return () => clearInterval(interval);
  }, [isPlaying, processSteps.length]);

  const handleStepClick = (index: number) => {
    setCurrentStep(index);
    setIsPlaying(false);
  };

  const togglePlayback = () => {
    setIsPlaying(!isPlaying);
  };

  const currentStepData = processSteps[currentStep];
  const title = lang === 'zh' ? currentStepData.title.zh : currentStepData.title.en;
  const description = lang === 'zh' ? currentStepData.description.zh : currentStepData.description.en;

  return (
    <motion.div
      initial={{ opacity: 0, x: 100 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -100 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen w-full bg-brand-bg p-8"
    >
      <header className="mb-12">
        <button onClick={onBack} className="flex items-center gap-2 text-brand-text hover:text-brand-accent transition-colors mb-4">
          <ArrowLeft size={20} />
          {t.ui.backToCollections[lang]}
        </button>
        <div className="flex items-center justify-between">
          <div>
            <h1 className="font-serif text-5xl text-brand-text font-bold">{t.process.title[lang]}</h1>
            <p className="text-brand-text text-lg mt-2 max-w-2xl">{t.process.description[lang]}</p>
          </div>
          <button
            onClick={togglePlayback}
            className="flex items-center gap-2 bg-brand-accent text-white px-6 py-3 rounded-full hover:bg-opacity-90 transition-colors"
          >
            {isPlaying ? <Pause size={20} /> : <Play size={20} />}
            {isPlaying ? t.process.pause[lang] : t.process.play[lang]}
          </button>
        </div>
      </header>

      <main className="max-w-6xl mx-auto">
        {/* Step Navigation - Moved to top */}
        <div className="mb-12">
          <div className="grid grid-cols-2 sm:grid-cols-4 lg:grid-cols-8 gap-4">
            {processSteps.map((step, index) => (
              <motion.button
                key={index}
                onClick={() => handleStepClick(index)}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className={`p-3 rounded-lg border-2 transition-all ${
                  currentStep === index
                    ? 'border-brand-accent bg-brand-accent text-white'
                    : 'border-gray-200 bg-white text-brand-text hover:border-brand-accent'
                }`}
              >
                <div className="w-8 h-8 mx-auto mb-2">
                  <AnimatedIcon index={index} isActive={currentStep === index} />
                </div>
                <p className="text-xs font-medium text-center">
                  {lang === 'zh' ? step.title.zh : step.title.en}
                </p>
              </motion.button>
            ))}
          </div>

          {/* Progress Bar */}
          <div className="bg-gray-200 rounded-full h-2 overflow-hidden mt-6">
            <motion.div
              className="bg-brand-accent h-full rounded-full"
              initial={{ width: 0 }}
              animate={{ width: `${((currentStep + 1) / processSteps.length) * 100}%` }}
              transition={{ duration: 0.5 }}
            />
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
          {/* Animation Area */}
          <div className="bg-white rounded-2xl p-8 shadow-lg">
            <AnimatePresence mode="wait">
              <motion.div
                key={currentStep}
                initial={{ opacity: 0, scale: 0.8, rotate: -10 }}
                animate={{ opacity: 1, scale: 1, rotate: 0 }}
                exit={{ opacity: 0, scale: 0.8, rotate: 10 }}
                transition={{ duration: 0.6, type: 'spring', stiffness: 200 }}
                className="flex flex-col items-center justify-center h-96"
              >
                <div className="w-48 h-48 mb-6">
                  <AnimatedIcon index={currentStep} isActive={true} large={true} />
                </div>
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: 0.3 }}
                  className="text-center"
                >
                  <h2 className="font-serif text-3xl font-bold text-brand-text mb-2">{title}</h2>
                  <div className="w-16 h-1 bg-brand-accent mx-auto rounded-full"></div>
                </motion.div>
              </motion.div>
            </AnimatePresence>
          </div>

          {/* Content Area */}
          <div className="space-y-8">
            <motion.div
              key={`content-${currentStep}`}
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5 }}
              className="bg-white p-6 rounded-lg shadow-sm"
            >
              <h3 className="font-serif text-2xl font-bold text-brand-text mb-4">
                {t.process.stepLabel[lang]} {currentStep + 1}: {title}
              </h3>
              <p className="text-brand-text leading-relaxed text-lg">
                {description}
              </p>
            </motion.div>
          </div>
        </div>
      </main>
    </motion.div>
  );
};

interface AnimatedIconProps {
  index: number;
  isActive?: boolean;
  large?: boolean;
}

const AnimatedIcon: React.FC<AnimatedIconProps> = ({ index, isActive = false, large = false }) => {
  const size = large ? 192 : 32;
  const viewBox = large ? "0 0 160 160" : "0 0 32 32";
  const scale = large ? 1 : 0.2;

  switch (index) {
    case 0:
      return <BrewTeaIcon size={size} viewBox={viewBox} scale={scale} isActive={isActive} />;
    case 1:
      return <AddSugarIcon size={size} viewBox={viewBox} scale={scale} isActive={isActive} />;
    case 2:
      return <CoolDownIcon size={size} viewBox={viewBox} scale={scale} isActive={isActive} />;
    case 3:
      return <AddScobyIcon size={size} viewBox={viewBox} scale={scale} isActive={isActive} />;
    case 4:
      return <FermentIcon size={size} viewBox={viewBox} scale={scale} isActive={isActive} />;
    case 5:
      return <BottleIcon size={size} viewBox={viewBox} scale={scale} isActive={isActive} />;
    case 6:
      return <FlavoringIcon size={size} viewBox={viewBox} scale={scale} isActive={isActive} />;
    default:
      return <GlassEnjoyIcon size={size} viewBox={viewBox} scale={scale} isActive={isActive} />;
  }
};

interface IconProps {
  size: number;
  viewBox: string;
  scale: number;
  isActive: boolean;
}

// 1. Brew Tea
const BrewTeaIcon: React.FC<IconProps> = ({ size, viewBox, scale, isActive }) => {
  return (
    <svg width={size} height={size} viewBox={viewBox} aria-label="Brew Tea Icon">
      <defs>
        <linearGradient id="teaFill" x1="0" y1="0" x2="0" y2="1">
          <stop offset="0%" stopColor="#C3E8D4" />
          <stop offset="100%" stopColor="#7CC8A1" />
        </linearGradient>
      </defs>
      <g transform={`scale(${scale})`}>
        <motion.g 
          initial={{ y: 0 }} 
          animate={isActive ? { y: [0, -6, 0] } : { y: 0 }} 
          transition={{ repeat: isActive ? Infinity : 0, duration: 2 }}
        >
          <rect x="40" y="60" width="80" height="50" rx="12" fill="url(#teaFill)" stroke="#228B5A" strokeWidth="3" />
          <rect x="30" y="50" width="100" height="10" rx="5" fill="#228B5A" />
          <circle cx="120" cy="80" r="12" fill="none" stroke="#228B5A" strokeWidth="4" />
        </motion.g>
        {isActive && (
          <>
            <Steam x={60} />
            <Steam x={80} delay={0.3} />
            <Steam x={100} delay={0.6} />
          </>
        )}
      </g>
    </svg>
  );
};

const Steam: React.FC<{ x: number; delay?: number }> = ({ x, delay = 0 }) => (
  <motion.path
    d={`M${x},55 q5,-10 0,-20`}
    fill="none"
    stroke="#94B8A6"
    strokeWidth="3"
    strokeLinecap="round"
    initial={{ opacity: 0, pathLength: 0, y: 0 }}
    animate={{ opacity: [0, 1, 0], pathLength: [0, 1, 1], y: [-2, -12] }}
    transition={{ duration: 2, repeat: Infinity, delay }}
  />
);

