import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { ArrowLeft } from 'lucide-react';
import { CONSTANTS } from './data';
import { ASSETS } from './assetManifest';

interface AboutPageProps {
  onBack: () => void;
  lang: 'en' | 'zh';
}

const AboutPage: React.FC<AboutPageProps> = ({ onBack, lang }) => {
  const [activeSection, setActiveSection] = useState<'founder' | 'origins' | 'lab' | 'health' | 'cocktails'>('founder');

  const t = CONSTANTS.I18N;
  const aboutData = CONSTANTS.ABOUT_SECTIONS;

  const sections = [
    { id: 'founder', label: t.about.sections.founder[lang] },
    { id: 'origins', label: t.about.sections.origins[lang] },
    { id: 'lab', label: t.about.sections.lab[lang] },
    { id: 'health', label: t.about.sections.health[lang] },
    { id: 'cocktails', label: t.about.sections.cocktails[lang] }
  ] as const;

  const currentContent = aboutData[activeSection];
  const title = lang === 'zh' ? currentContent.title.zh : currentContent.title.en;
  const content = lang === 'zh' ? currentContent.content.zh : currentContent.content.en;

  const imageKeys: string[] = currentContent.imageKeys || [];
  const images = imageKeys
    .map((k) => ASSETS.about?.[k]?.path)
    .filter(Boolean) as string[];

  return (
    <motion.div
      initial={{ opacity: 0, x: 100 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -100 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen w-full bg-brand-bg p-8"
    >
      <header className="mb-12">
        <button onClick={onBack} className="flex items-center gap-2 text-brand-text hover:text-brand-accent transition-colors mb-4">
          <ArrowLeft size={20} />
          {t.ui.backToCollections[lang]}
        </button>
        <h1 className="font-serif text-5xl text-brand-text font-bold">{t.about.title[lang]}</h1>
        <p className="text-brand-text text-lg mt-2 max-w-2xl">{t.about.description[lang]}</p>
      </header>

      <main className="max-w-6xl mx-auto">
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
          <div className="lg:col-span-1">
            <div className="space-y-2">
              {sections.map((section) => (
                <button
                  key={section.id}
                  onClick={() => setActiveSection(section.id)}
                  className={`w-full text-left p-4 rounded-lg transition-colors ${
                    activeSection === section.id
                      ? 'bg-brand-accent text-white'
                      : 'bg-white text-brand-text hover:bg-gray-50'
                  }`}
                >
                  {section.label}
                </button>
              ))}
            </div>
          </div>

          <div className="lg:col-span-3">
            <motion.div
              key={activeSection}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.3 }}
              className="bg-white p-8 rounded-lg shadow-sm"
            >
              <h2 className="font-serif text-3xl font-bold text-brand-text mb-6">{title}</h2>
              <div className="prose prose-lg max-w-none text-brand-text">
                {content.split('\n\n').map((paragraph, index) => (
                  <p key={index} className="mb-4 leading-relaxed">
                    {paragraph}
                  </p>
                ))}
              </div>

              {images.length > 0 && (
                <div className="mt-8 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
                  {images.map((src, idx) => (
                    <motion.div
                      key={`${activeSection}-img-${idx}`}
                      whileHover={{ scale: 1.02 }}
                      transition={{ type: 'spring', stiffness: 250, damping: 18 }}
                      className="overflow-hidden rounded-xl shadow-md bg-gray-100"
                    >
                      <img
                        src={src}
                        alt={`${title} - photo ${idx + 1}`}
                        className="w-full h-56 md:h-64 object-cover"
                      />
                    </motion.div>
                  ))}
                </div>
              )}
            </motion.div>
          </div>
        </div>
      </main>
    </motion.div>
  );
};

export default AboutPage;
