import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { ArrowLeft, MapPin } from 'lucide-react';
import { CONSTANTS } from './data';

interface WhereToFindPageProps {
  onBack: () => void;
  lang: 'en' | 'zh';
}

type CityId = keyof typeof CONSTANTS.LOCATIONS;

const WhereToFindPage: React.FC<WhereToFindPageProps> = ({ onBack, lang }) => {
  const cities = Object.keys(CONSTANTS.LOCATIONS) as CityId[];
  const [selectedCity, setSelectedCity] = useState<CityId>(cities[0]);

  const t = CONSTANTS.I18N;
  const locationsData = CONSTANTS.LOCATIONS;

  const venues = locationsData[selectedCity];

  return (
    <motion.div
      initial={{ opacity: 0, x: 100 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -100 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen w-full bg-brand-bg p-8"
    >
      <header className="mb-12">
        <button onClick={onBack} className="flex items-center gap-2 text-brand-text hover:text-brand-accent transition-colors mb-4">
          <ArrowLeft size={20} />
          {t.ui.backToCollections[lang]}
        </button>
        <h1 className="font-serif text-5xl text-brand-text font-bold">{t.locations.title[lang]}</h1>
        <p className="text-brand-text text-lg mt-2 max-w-2xl">{t.locations.description[lang]}</p>
      </header>

      <main className="max-w-6xl mx-auto">
        <div className="flex flex-wrap gap-4 mb-8">
          {cities.map((cityId) => (
            <button
              key={cityId}
              onClick={() => setSelectedCity(cityId)}
              className={`px-6 py-3 rounded-full text-lg font-semibold transition-colors ${
                selectedCity === cityId
                  ? 'bg-brand-accent text-white'
                  : 'bg-white text-brand-text hover:bg-gray-100'
              }`}
            >
              {t.locations.cities[cityId][lang]}
            </button>
          ))}
        </div>

        <div>
          <h2 className="font-serif text-3xl text-brand-text font-bold mb-6">
            {t.locations.venuesIn[lang]} {t.locations.cities[selectedCity][lang]}
          </h2>
          <motion.div
            key={selectedCity}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
          >
            {venues.map((venue, index) => (
              <motion.div
                key={`${selectedCity}-${index}`}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.3, delay: index * 0.05 }}
                className="bg-white p-6 rounded-lg shadow-sm flex items-start gap-4"
              >
                <div className="bg-brand-accent/10 p-3 rounded-full mt-1">
                  <MapPin size={24} className="text-brand-accent" />
                </div>
                <div>
                  <h3 className="font-bold text-xl text-brand-text">{venue.name}</h3>
                  <p className="text-gray-600">{venue.address}</p>
                </div>
              </motion.div>
            ))}
          </motion.div>
        </div>
      </main>
    </motion.div>
  );
};

export default WhereToFindPage;
