import React from 'react';
import { motion } from 'framer-motion';
import { ArrowLeft } from 'lucide-react';
import { ASSETS } from './assetManifest';
import { CONSTANTS } from './data';

type Product = {
  id: string;
  name: string;
  imageKey: string;
  size: string;
  description: string;
};

interface CollectionPageProps {
  collection: {
    name: string;
    description: string;
    items: Product[];
  };
  collectionId: 'cuvee' | 'standard';
  onSelectProduct: (product: Product) => void;
  onBack: () => void;
  lang: 'en' | 'zh';
}

const CollectionPage: React.FC<CollectionPageProps> = ({ collection, collectionId, onSelectProduct, onBack, lang }) => {
  const t = CONSTANTS.I18N;
  const localizedName = t.collections[collectionId]?.name[lang] || collection.name;
  const localizedDesc = t.collections[collectionId]?.description[lang] || collection.description;
  const backLabel = t.ui.backToCollections[lang];

  return (
    <motion.div
      initial={{ opacity: 0, x: 100 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -100 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen w-full bg-brand-bg p-8"
    >
      <header className="mb-12">
        <button onClick={onBack} className="flex items-center gap-2 text-brand-text hover:text-brand-accent transition-colors mb-4">
          <ArrowLeft size={20} />
          {backLabel}
        </button>
        <h1 className="font-serif text-5xl text-brand-text font-bold">{localizedName}</h1>
        <p className="text-brand-text text-lg mt-2 max-w-2xl">{localizedDesc}</p>
      </header>

      <main className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
        {collection.items.map((product, index) => (
          <motion.div
            key={product.id}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: index * 0.1 }}
            whileHover={{ y: -5 }}
            onClick={() => onSelectProduct(product)}
            className="bg-white rounded-lg shadow-md overflow-hidden cursor-pointer flex flex-col"
          >
            <div className="h-64 bg-brand-primary flex items-center justify-center p-4">
              <img
                src={ASSETS.products[product.imageKey].path}
                alt={product.name}
                className="max-h-full w-auto object-contain"
              />
            </div>
            <div className="p-4 text-center flex-grow flex flex-col justify-between">
              <h3 className="font-serif text-xl text-brand-text">
                {lang === 'zh'
                  ? (CONSTANTS.I18N.products[product.id]?.name || product.name)
                  : product.name}
              </h3>
              <p className="text-sm text-gray-500 mt-1">{product.size}</p>
            </div>
          </motion.div>
        ))}
      </main>
    </motion.div>
  );
};

export default CollectionPage;
