import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { ArrowLeft, FileText, Leaf, Beaker, Clock } from 'lucide-react';
import { ASSETS } from './assetManifest';
import { CONSTANTS } from './data';

type Product = {
  id: string;
  name: string;
  imageKey: string;
  size: string;
  description: string;
  tastingNotes: string;
  teaDetails: string;
  ingredients: string;
  fermentation: string;
};

interface ProductDetailPageProps {
  product: Product;
  onBack: () => void;
  lang: 'en' | 'zh';
}

const ProductDetailPage: React.FC<ProductDetailPageProps> = ({ product, onBack, lang }) => {
  const [activeCard, setActiveCard] = useState<'description' | 'tea' | 'ingredients' | 'fermentation'>('description');

  const t = CONSTANTS.I18N;
  const pT = CONSTANTS.I18N.products[product.id] || {};

  const name = lang === 'zh' ? (pT.name || product.name) : product.name;
  const description = lang === 'zh' ? (pT.description || product.description) : product.description;
  const tastingNotes = lang === 'zh' ? (pT.tastingNotes || product.tastingNotes) : product.tastingNotes;
  const teaDetails = lang === 'zh' ? (pT.teaDetails || product.teaDetails) : product.teaDetails;
  const ingredients = lang === 'zh' ? (pT.ingredients || product.ingredients) : product.ingredients;
  const fermentation = lang === 'zh' ? (pT.fermentation || product.fermentation) : product.fermentation;

  const tabs = [
    { id: 'description', label: t.ui.overview[lang], icon: FileText },
    { id: 'tea', label: t.ui.tea[lang], icon: Leaf },
    { id: 'ingredients', label: t.ui.ingredients[lang], icon: Beaker },
    { id: 'fermentation', label: t.ui.fermentation[lang], icon: Clock }
  ] as const;

  const cardContent = {
    description: (
      <>
        <h2 className="font-serif text-2xl font-semibold mb-2">{t.ui.description[lang]}</h2>
        <p className="font-sans text-base leading-relaxed">{description}</p>
        {tastingNotes && (
          <>
            <h2 className="font-serif text-2xl font-semibold mb-2 mt-4">{t.ui.tastingNotes[lang]}</h2>
            <p className="font-sans text-base leading-relaxed">{tastingNotes}</p>
          </>
        )}
      </>
    ),
    tea: (
      <>
        <h2 className="font-serif text-2xl font-semibold mb-2">{t.ui.tea[lang]}</h2>
        <p className="font-sans text-base leading-relaxed">{teaDetails}</p>
      </>
    ),
    ingredients: (
      <>
        <h2 className="font-serif text-2xl font-semibold mb-2">{t.ui.ingredients[lang]}</h2>
        <p className="font-sans text-base leading-relaxed">{ingredients}</p>
      </>
    ),
    fermentation: (
      <>
        <h2 className="font-serif text-2xl font-semibold mb-2">{t.ui.fermentation[lang]}</h2>
        <p className="font-sans text-base leading-relaxed">{fermentation}</p>
      </>
    )
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: 100 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -100 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen w-full bg-brand-bg p-8"
    >
      <header className="mb-8">
        <button onClick={onBack} className="flex items-center gap-2 text-brand-text hover:text-brand-accent transition-colors mb-4">
          <ArrowLeft size={20} />
          {t.ui.backToCollection[lang]}
        </button>
      </header>

      <main className="max-w-5xl mx-auto grid grid-cols-1 md:grid-cols-2 gap-12 items-start">
        <motion.div
          initial={{ opacity: 0, scale: 0.8 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          className="bg-brand-primary rounded-lg h-[60vh] flex items-center justify-center p-8"
        >
          <img
            src={ASSETS.products[product.imageKey].path}
            alt={name}
            className="max-h-full w-auto object-contain"
          />
        </motion.div>
        
        <div className="text-brand-text">
          <h1 className="font-serif text-5xl font-bold">{name}</h1>
          <p className="text-lg text-gray-500 mt-1 mb-8">{product.size}</p>

          {/* Tab Navigation */}
          <div className="mb-8">
            <div className="grid grid-cols-2 gap-3">
              {tabs.map((tab) => {
                const IconComponent = tab.icon;
                return (
                  <motion.button
                    key={tab.id}
                    onClick={() => setActiveCard(tab.id)}
                    whileHover={{ scale: 1.02 }}
                    whileTap={{ scale: 0.98 }}
                    className={`p-4 rounded-lg border-2 transition-all ${
                      activeCard === tab.id
                        ? 'border-brand-accent bg-brand-accent text-white'
                        : 'border-gray-200 bg-white text-brand-text hover:border-brand-accent'
                    }`}
                  >
                    <div className="flex flex-col items-center gap-2">
                      <IconComponent size={20} />
                      <p className="text-sm font-medium text-center">
                        {tab.label}
                      </p>
                    </div>
                  </motion.button>
                );
              })}
            </div>
          </div>

          <motion.div
            key={activeCard}
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3 }}
            className="space-y-6 bg-white p-6 rounded-lg shadow-sm"
          >
            {cardContent[activeCard]}
          </motion.div>
        </div>
      </main>
    </motion.div>
  );
};

export default ProductDetailPage;
