import React, { useState } from 'react';
import { AnimatePresence } from 'framer-motion';
import HomePage from './HomePage';
import CollectionPage from './CollectionPage';
import ProductDetailPage from './ProductDetailPage';
import AboutPage from './AboutPage';
import KombuchaProcessPage from './KombuchaProcessPage';
import WhereToFindPage from './WhereToFindPage';
import { CONSTANTS } from './data';

type Product = {
  id: string;
  name: string;
  imageKey: string;
  size: string;
  description: string;
  tastingNotes?: string;
  teaDetails: string;
  ingredients: string;
  fermentation: string;
};

type ViewState =
  | { page: 'home' }
  | { page: 'collection'; collectionId: 'cuvee' | 'standard' }
  | { page: 'product'; collectionId: 'cuvee' | 'standard'; product: Product }
  | { page: 'about' }
  | { page: 'process' }
  | { page: 'locations' };

type Lang = 'en' | 'zh';

export default function App() {
  const [view, setView] = useState<ViewState>({ page: 'home' });
  const [lang, setLang] = useState<Lang>('en');

  const handleSelectCollection = (collectionId: 'cuvee' | 'standard') => {
    setView({ page: 'collection', collectionId });
  };

  const handleSelectAbout = () => {
    setView({ page: 'about' });
  };

  const handleSelectProcess = () => {
    setView({ page: 'process' });
  };

  const handleSelectLocations = () => {
    setView({ page: 'locations' });
  };

  const handleSelectProduct = (product: Product) => {
    if (view.page === 'collection') {
      setView({ page: 'product', collectionId: view.collectionId, product });
    }
  };

  const handleBack = () => {
    if (view.page === 'product') {
      setView({ page: 'collection', collectionId: view.collectionId });
    } else if (view.page === 'collection' || view.page === 'about' || view.page === 'process' || view.page === 'locations') {
      setView({ page: 'home' });
    }
  };

  const renderContent = () => {
    switch (view.page) {
      case 'home':
        return (
          <HomePage
            key="home"
            onSelectCollection={handleSelectCollection}
            onSelectAbout={handleSelectAbout}
            onSelectProcess={handleSelectProcess}
            onSelectLocations={handleSelectLocations}
            lang={lang}
            onChangeLang={setLang}
          />
        );
      case 'collection': {
        const collection = CONSTANTS.PRODUCTS[view.collectionId];
        return (
          <CollectionPage
            key="collection"
            collection={collection}
            collectionId={view.collectionId}
            onSelectProduct={handleSelectProduct}
            onBack={handleBack}
            lang={lang}
          />
        );
      }
      case 'product':
        return (
          <ProductDetailPage
            key="product"
            product={view.product}
            onBack={handleBack}
            lang={lang}
          />
        );
      case 'about':
        return (
          <AboutPage
            key="about"
            onBack={handleBack}
            lang={lang}
          />
        );
      case 'process':
        return (
          <KombuchaProcessPage
            key="process"
            onBack={handleBack}
            lang={lang}
          />
        );
      case 'locations':
        return (
          <WhereToFindPage
            key="locations"
            onBack={handleBack}
            lang={lang}
          />
        );
      default:
        return (
          <HomePage
            key="home"
            onSelectCollection={handleSelectCollection}
            onSelectAbout={handleSelectAbout}
            onSelectProcess={handleSelectProcess}
            onSelectLocations={handleSelectLocations}
            lang={lang}
            onChangeLang={setLang}
          />
        );
    }
  };

  return (
    <div className="App bg-brand-bg">
      <AnimatePresence mode="wait">
        {renderContent()}
      </AnimatePresence>
    </div>
  );
}
